export default {
  async created() {
    await this.getMenus();
  },
  data() {
    return {
      Menus: [],
      errors: []
    };
  },
  methods: {
    async getMenus() {
      try {
        const response = await this.$axios.get(
          this.$axiosUrl+"menus"
        );
        this.Menus = response.data
      } catch (error) {
        this.errors.push(error);
      }
    }
  }
};
